.footer {
	position: relative;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	width: 100%;
	max-width: 100%;
	overflow-x: hidden;
	height: calc(100vh - vw(70));
	background-color: #07171D;
	background-image: url('../img/footer__bg.png');
	background-position: left bottom;
	background-repeat: repeat-x;
	z-index: 30;

	@include max(xl) {
		height: calc(100vh - vwf(55));
	}

	&__animation {
		position: absolute;
		top: vw(70);

		@include max(xl) {
			top: vwf(55);
		}

		@include max(md) {
			position: relative;
			flex-flow: column nowrap;
			width: 100vw;
			height: auto;
			height: 100%;
			top: 0 !important;
		}

		&--personally {
			top: auto;
			height: calc(100vh - vw(00));
			position: relative;
			flex-flow: column nowrap;
			width: 100%;


			@include max(xl) {
				top: auto;
				height: calc(100vh - vw(00));
				position: relative;
				flex-flow: column nowrap;
				width: 100%;
			}

			@include max(md) {
				height: 100%;
			}

			.footer__menu {
				height: calc(100vh - vw(00));
				padding-top: vw(96);

				@include max(xl) {
					height: calc(100vh - vw(00));
					padding-top: vw(96);
				}

				@include max(md) {
					padding: vw2(20);
					height: 100%;
				}
			}

			.footer__logo {
				top: vw(100);
			}

			.footer__title {
				top: calc(50% - vw(135));
			}
		}
	}

	&__title {
		position: absolute;
		top: calc(50% - vw(170));
		left: 50%;
		white-space: nowrap;
		z-index: 10;
		color: rgba(255, 255, 255, 0.2);


		@include max(xl) {
			top: calc(50% - vw(170));
		}

		@include max(md) {
			position: relative;
			top: auto;
			left: auto;
			white-space: inherit;
			width: 100%;
			max-width: 95%;
			font-size: vw2(50);
			font-style: normal;
			font-weight: 500;
			line-height: 1.2;
			margin-bottom: vw2(40);
			margin-right: vw2(20);
			margin-left: vw2(20);
			margin-top: vw2(40);
		}
	}

	&__menu {
		position: absolute;
		top: 0;
		right: -100%;
		width: vw(473);
		height: calc(100vh - vw(70));
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-flow: column nowrap;
		padding: vw(25) vw(37);
		padding-bottom: vw(25);
		background: #fff;
		z-index: 900;
		z-index: 90;
		overflow-y: auto;
		scrollbar-width: thin;
		scrollbar-color: #99c4d3 rgba(0, 0, 0, 0);

		&::-webkit-scrollbar {
			height: 2px;
			width: 2px;
		}

		&::-webkit-scrollbar-track {
			background: rgba(0, 0, 0, 0);
		}

		&::-webkit-scrollbar-thumb {
			background-color: #99c4d3;
			border-radius: 10px;
			border: none;
		}

		@include max(xl) {
			width: vwf(473);
			height: calc(100vh - vwf(55));
			padding: vwf(25) vwf(37);
			padding-bottom: vwf(25);
		}

		@include max(md) {
			position: relative;
			right: auto;
			width: 100%;
			display: flex;
			padding: vw2(20);
			margin-bottom: vw2(40);
			height: 100%;
			background: transparent;
		}

		&__list {
			position: relative;
			display: flex;
			justify-content: flex-start;
			align-items: flex-start;
			flex-flow: column nowrap;
			width: 100%;
			border-top: 1px solid rgba(0, 0, 0, 0.2);
			padding-top: vw(26);
			margin-bottom: vw(30);
			list-style-type: none;

			@include max(xl) {
				padding-top: vwf(26);
				margin-bottom: vwf(30);
			}

			&__item {
				position: relative;
				display: flex;
				flex-direction: column;
				gap: vw(7);
				color: #07171D;
				font-size: vw(14);
				font-style: normal;
				font-weight: 600;
				line-height: normal;
				text-transform: uppercase;
				margin-bottom: vw(19);

				@include max(xl) {
					gap: vwf(7);
					font-size: vwf(14);
					margin-bottom: vwf(19);
				}

				@include max(md) {
					font-size: vw2(24);
					line-height: 1.2;
					color: #fff;
					gap: vw2(5);
				}

				a {
					position: relative;
					text-decoration: none;
					color: #07171D;
					font-size: vw(14);
					font-style: normal;
					font-weight: 600;
					line-height: normal;
					text-transform: uppercase;
					transition: all $tr-time-fast;
					cursor: pointer;

					@include max(xl) {
						font-size: vwf(14);
					}

					@include max(md) {
						font-size: vw2(24);
						line-height: 1.2;
						color: #fff;
						font-weight: 400;
					}

					&:hover {
						transition: all $tr-time-fast;
						color: $blueHover;
					}

				}

				span {
					position: relative;
					color: #07171D;
					font-size: vw(14);
					font-style: normal;
					font-weight: 600;
					line-height: normal;
					text-transform: uppercase;

					@include max(xl) {
						font-size: vwf(14);
					}

					@include max(md) {
						font-size: vw2(24);
						line-height: 1.2;
						color: #fff;
						font-weight: 400;
					}
				}
			}

			&__title {
				position: absolute;
				top: vw(26);
				right: vw(0);
				color: rgba(7, 23, 29, 0.2);
				font-size: vw(14);
				font-style: normal;
				font-weight: 600;
				line-height: normal;
				text-transform: uppercase;
				margin-bottom: vw(19);

				@include max(xl) {
					top: vwf(26);
					font-size: vwf(14);
					margin-bottom: vwf(19);
				}

				@include max(md) {
					position: relative;
					top: auto;
					right: auto;
					font-size: vw2(28);
					font-style: normal;
					line-height: 1.2;
					color: rgba(255, 255, 255, 0.2);
					margin-bottom: vw2(20);
				}
			}
		}

		&__social {
			display: flex;
			align-items: center;
			margin-top: vw(30);
			gap: vw(18);
			flex-shrink: 0;

			@include max(xl) {
				margin-top: vwf(30);
				gap: vwf(18);
			}

			@include max(md) {
				gap: vw2(18);
				margin-top: vw2(30);
			}

			.new-btn {
				width: vwf(20);
				height: vwf(20);

				@include max(xl) {
					width: vwf(20);
					height: vwf(20);
				}

				@include max(md) {
					width: vw2(30);
					height: vw2(30);
				}

				&__icon {
					width: vwf(20);
					height: vwf(20);

					@include max(xl) {
						width: vwf(20);
						height: vwf(20);
					}

					@include max(md) {
						width: vw2(30);
						height: vw2(30);
					}
				}

				svg {
					width: 100%;
					height: 100%;

					@include max(xl) {
						width: 100%;
						height: 100%;
					}

					@include max(md) {
						width: 100%;
						height: 100%;
					}

					use {
						fill: $fontBlack;
						stroke: none;

						@include max(md) {
							fill: $white;
						}
					}
				}

				&:hover {
					svg {
						use {
							fill: $blueLight;
							stroke: none;
						}
					}
				}
			}
		}

