	&__btn {
		position: absolute;
		bottom: vw(33);
		left: vwf(25);
		z-index: 20;

		@include max(xl) {
			bottom: vwf(33);
		}

		@include max(md) {
			position: relative;
			top: auto;
			left: auto;
			bottom: auto;
			left: auto;
			margin-right: vw2(20);
			margin-left: vw2(20);
		}
	}

	&__logo {
		position: absolute;
		top: vw(30);
		left: vwf(25);
		display: none;
		width: vw(153);

		@include max(xl) {
			top: vwf(30);
			width: vwf(153);
		}

		@include max(md) {
			position: relative;
			margin-bottom: vw2(60);
			top: vw2(20);
			left: vw2(20);
			width: vw2(220);
		}

		img {
			position: relative;
			display: block;
			width: 100%;
		}
	}

	&__agreement {
		position: absolute;
		bottom: vw(33);
		right: vw(503);
		z-index: 20;
		display: flex;
		flex-flow: column nowrap;
		justify-content: flex-start;
		align-items: flex-end;
		text-align: right;

		@include max(xl) {
			bottom: vwf(33);
			right: vwf(503);
		}

		@include max(md) {
			position: relative;
			top: auto;
			left: auto;
			bottom: auto;
			right: auto;
			width: 100%;
			justify-content: center;
			align-items: center;
			margin-top: vw2(40);
			margin-bottom: vw2(40);
		}

		&__date {
			color: rgba(255, 255, 255, 0.2);
			margin-bottom: vw(14);

			@include max(xl) {
				margin-bottom: vwf(14);
			}

			@include max(md) {
				margin-bottom: vw2(10);
				text-align: center;
			}
		}

		&__text {
			transition: all $tr-time-fast;

			@include max(md) {
				text-align: center;
			}

			&:hover {
				transition: all $tr-time-fast;
				color: $blueHover;
			}
		}
	}
}