.slider-photo {
    overflow: hidden;

    &__slider {
        position: relative;
    }

    &__slide {
        display: flex;
        justify-content: center;
        align-items: center;
        height: vw(500);

        @include max(xl) {
            height: vwf(500);
        }

        @include max(md) {
            height: vw2(400);
        }
    }

    &__img {
        width: 100%;
        height: vw(500);

        @include max(xl) {
            height: vwf(500);
        }

        @include max(md) {
            height: vw2(400);
        }

        img {
            height: 100%;
            width: 100%;
            object-fit: contain;
        }
    }

    &__thumbs {
        position: relative;
        margin-top: vw(50);
        height: vw(114);

        @include max(xl) {
            margin-top: vwf(50);
            height: vwf(114);
        }

        @include max(md) {
            height: vw2(66);
            margin-top: vw2(30);
        }

        &:hover {
            .slider-photo__btn {
                opacity: 1;
            }
        }
    }

    &__thumb {
        height: 100%;
        width: vw(114);
        cursor: pointer;
        opacity: 0.45;
        transition: all $tr-time linear;

        @include max(xl) {
            width: vwf(114);
        }

        @include max(md) {
            width: vw2(66);
        }

        &.swiper-slide-thumb-active {
            opacity: 1;
        }

        &:hover {
            opacity: 0.8;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__btn {
        width: vw(59);
        height: vw(59);
        background-color: $blueLight;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        transition: $tr-time;
        top: vw(50);
        opacity: 0;

        @include max(xl) {
            width: vwf(59);
            height: vwf(59);
        }

        @include max(md) {
            width: vw2(40);
            height: vw2(40);
            opacity: 1;
            top: vw2(46);
        }

        &::after {
            content: none;
        }

        &:hover {
            background-color: $blueHover;
        }

        svg {
            width: vw(11);
            height: vw(8);

            @include max(xl) {
                width: vwf(11);
                height: vwf(8);
            }

            @include max(md) {
                width: vw2(11);
                height: vw2(8);
            }

            use {
                stroke: $white;
                fill: none;
                width: vw(11);
                height: vw(8);

                @include max(xl) {
                    width: vwf(11);
                    height: vwf(8);
                }

                @include max(md) {
                    width: vw2(11);
                    height: vw2(8);
                }
            }
        }

        &.swiper-button-prev {
            transform: rotate(180deg);
            left: vw(16);
        }

        &.swiper-button-next {
            right: vw(16);
        }
    }
}