.production-intro {
    padding: vw(115) 0 vw(68);
    min-height: 130vh;
    display: flex;
    background-size: cover;
    background-position: center;

    @include max(xl) {
        padding: vwf(115) 0 vwf(68);
    }

    @include max(md) {
        padding: vw2(100) 0 vw2(30);
        min-height: vw2(500);
    }

    &__container {
        min-height: 100%;
    }

    &__content {
        display: flex;
        flex-direction: column;
        min-height: 100%;
    }

    &__breadcrumbs {
        padding-left: vw(10);

        @include max(xl) {
            padding-left: vwf(10);
        }

        @include max(md) {
            padding-left: vw2(5);
        }
    }

    &__title {
        color: $white;
        font-size: vw(171);
        line-height: 0.97;

        @include max(xl) {
            font-size: vwf(171);
        }

        @include max(md) {
            font-size: vw2(50)
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
        gap: vw(20);
        margin-top: vw(30);
        max-width: vw(657);
        font-size: vw(32);
        font-weight: 400;
        line-height: 1.11;
        letter-spacing: -0.03em;
        color: $white;

        @include max(xl) {
            gap: vwf(20);
            margin-top: vwf(30);
            max-width: vwf(657);
            font-size: vwf(32);
        }

        @include max(md) {
            max-width: 100%;
            margin-top: vw2(50);
            font-size: vw2(40);
            font-size: vw2(24);
            gap: vw2(15);
        }
    }
}