.news-body {
    &__container {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        background-color: $greyBorder;
        gap: 1px;

        @include max(md) {
            grid-template-columns: minmax(0, 1fr);
        }
    }

    &__left {
        display: flex;
        flex-direction: column;
        background-color: $white;
        padding: vw(32) 0 vw(89);

        @include max(xl) {
            padding: vwf(32) 0 vwf(89);
        }

        @include max(md) {
            padding: vw2(32) 0 vw2(20);
        }
    }

    &__text {
        padding: 0 vw(25);

        @include max(xl) {
            padding: 0 vwf(25);
        }

        @include max(md) {
            padding: 0 vw2(20);
        }

        ul {
            margin: vw(88) 0 vw(73);

            @include max(xl) {
                margin: vwf(88) 0 vwf(73);
            }

            @include max(md) {
                margin: vw2(30) 0 vw2(25);
            }
        }
    }

    &__slider {
        margin-top: vw(31);
        padding: vw(81.5) vw(30);
        background-color: $fontBlack;

        @include max(xl) {
            margin-top: vwf(31);
            padding: vwf(81.5) vwf(30);
        }

        @include max(md) {
            margin-top: vw2(30);
            padding: vw2(60) vw2(20) vw2(30);
        }
    }

    &__share {
        margin-top: vw(156);
        margin-left: vw(25);

        @include max(xl) {
            margin-top: vwf(156);
            margin-left: vwf(25);
        }

        @include max(md) {
            margin-top: vw2(30);
            margin-left: vw2(20);
        }
    }

    .social-share {
        display: flex;
        flex-wrap: wrap;
        row-gap: vw(20);
        column-gap: vw(15);

        @include max(xl) {
            row-gap: vwf(20);
            column-gap: vwf(15);
        }

        @include max(md) {
            row-gap: vw2(20);
            column-gap: vw2(15);
        }

        &__title {
            flex-basis: 100%;
            font-size: vw(18);
            font-weight: 400;
            line-height: 1.389;
            color: $fontBlack;

            @include max(xl) {
                font-size: vwf(18);
            }

            @include max(md) {
                font-size: vw2(16);
            }
        }

        &__btn {
            width: vw(48);
            height: vw(48);
            background-color: #D3D3D3;

            @include max(xl) {
                width: vwf(48);
                height: vwf(48);
            }

            @include max(md) {
                width: vw2(48);
                height: vw2(48);
            }

            &:hover {
                background-color: $blueHover;
            }

            .new-btn__icon {
                width: vw(48);
                height: vw(48);

                @include max(xl) {
                    width: vwf(48);
                    height: vwf(48);
                }

                @include max(md) {
                    width: vw2(48);
                    height: vw2(48);
                }

                svg,
                svg use {
                    stroke: none;
                    width: vw(16) !important;
                    height: vw(16) !important;

                    @include max(xl) {
                        width: vwf(16) !important;
                        height: vwf(16) !important;
                    }

                    @include max(md) {
                        width: vw2(16) !important;
                        height: vw2(16) !important;
                    }
                }
            }
        }
    }

    &__right {
        background-color: $white;
        padding: vw(28) vw(25) vw(89) vw(52);

        @include max(xl) {
            padding: vwf(28) vwf(25) vwf(89) vwf(52);
        }

        @include max(md) {
            padding: vw2(40) vw2(20);
        }
    }

    &__offer {
        font-size: vw(32);
        font-weight: 500;
        line-height: 0.97;
        color: $fontBlack;

        @include max(xl) {
            font-size: vwf(32);
        }

        @include max(md) {
            font-size: vw2(32);
        }
    }

    &__btn.new-btn {
        margin-top: vw(75);

        @include max(xl) {
            margin-top: vwf(75);
        }

        @include max(md) {
            margin-top: vw2(30);
        }

        &:hover {
            background-color: $blueHover;

            .new-btn__text {
                color: $white;
            }

            .new-btn__icon {
                background-color: $white;

                svg use {
                    stroke: $fontBlack;
                }
            }
        }
    }
}