.career-numbers {
    position: relative;
    height: vw(520);
    background-image: url(../img/career_numbers_bg.png);
    background-repeat: no-repeat;
    background-size: vw(1456) vw(284);
    background-position: right vw(-127) bottom vw(106);
    z-index: 5;

    @include max(xl) {
        height: vwf(520);
        background-size: vwf(1456) vwf(284);
        background-position: right vwf(-127) bottom vwf(106);
    }

    @include max(md) {
        height: auto;
        background-image: none;
    }

    &__title {
        padding: 0 0 vw(30) vw(25);
        font-size: vw(20);
        font-weight: 600;
        line-height: 1.2;
        color: $fontBlack;
        text-transform: uppercase;

        @include max(xl) {
            padding: 0 0 vwf(30) vwf(25);
            font-size: vwf(20);
        }

        @include max(md) {
            padding: 0 0 vw2(30) vw2(20);
            font-size: vw2(32);
        }
    }

    &__cards {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        width: 100%;
        z-index: 10;

        @include max(md) {
            position: relative;
            top: auto;
            left: auto;
        }

        .layout--trio {
            position: relative;

            &__col {
                flex-flow: column nowrap;
                padding-bottom: vw(0);

                &:nth-child(1) {
                    padding-top: vw(0);
                }

                &:nth-child(3) {
                    padding-top: vw(0);
                }
            }
        }
    }

    .number-card {
        position: relative;
        display: flex;
        width: 100%;
        min-height: vw(640);
        justify-content: space-between;
        align-items: flex-start;
        flex-flow: column nowrap;
        border: 1px solid rgba(255, 255, 255, 0.11);
        background: $fontBlack;
        padding: vw(30) vw(40);

        @include max(xl) {
            min-height: vwf(640);
            padding: vwf(30) vwf(40);
        }

        @include max(md) {
            justify-content: flex-start;
            padding: vw2(20);
            min-height: vw2(300);
            padding-bottom: vw2(50);
        }

        &__num {
            color: $blueLight;
            font-size: vw(112);
            font-style: normal;
            font-weight: 500;
            line-height: 97.228%;
            text-transform: uppercase;

            @include max(xl) {
                font-size: vwf(112);
            }

            @include max(md) {
                font-size: vw2(95);
                line-height: 1.2;
                margin-bottom: vw2(40);
            }
        }

        &__title {
            margin-top: auto;
            margin-bottom: vw(37);
            max-width: vw(357);
            text-transform: uppercase;

            @include max(xl) {
                margin-bottom: vwf(37);
                max-width: vwf(357);
            }

            @include max(md) {
                font-size: vw2(32);
                width: 100%;
                max-width: 100%;
            }
        }
    }
}