.ap-offer {
    position: absolute;
    top: 0;
    left: 0;
    background-color: $blueLight;
    background-image: url("../img/ap_waves_blue.png");
    background-repeat: no-repeat;
    background-size: vw(861) auto;
    background-position: right vw(-35) top vw(160);
    z-index: 45;
    width: 100%;

    @include max(xl) {
        background-size: vwf(861) auto;
        background-position: right vwf(-35) top vwf(160);
    }

    @include max(md) {
        position: relative;
        background-size: vw2(260) auto;
        background-position: right vw2(-35) bottom vw2(20);
    }

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        padding: vw(20) 0 vw(31);

        @include max(xl) {
            padding: vwf(20) 0 vwf(31);
        }

        @include max(md) {
            padding: vw2(40) 0;
            flex-direction: column;
            gap: vw2(24);
        }
    }

    &__title {
        max-width: vw(723);
        font-size: vw(82);
        font-weight: 500;
        line-height: 0.97;
        text-transform: uppercase;
        color: $white;

        @include max(xl) {
            max-width: vwf(723);
            font-size: vwf(82);
        }

        @include max(md) {
            max-width: 100%;
            font-size: vw2(32);
        }
    }

    &__wrapper {
        flex-basis: 100%;
        margin-top: vw(244);

        @include max(xl) {
            margin-top: vwf(244);
        }

        @include max(md) {
            flex-basis: auto;
            margin-top: vw(25);
        }
    }

    &__text {
        max-width: vw(535);
        font-size: vw(32);
        font-weight: 400;
        line-height: 1;
        letter-spacing: -0.03em;
        color: $white;

        @include max(xl) {
            max-width: vwf(535);
            font-size: vwf(32);
        }

        @include max(md) {
            max-width: vw2(300);
            font-size: vw2(20);
        }
    }

    &__btn.new-btn {
        @include max(md) {
            order: 2;
        }

        &:hover {
            background-color: $fontBlack;

            .new-btn__text {
                color: $white;
            }

            .new-btn__icon {
                background-color: $white;

                svg use {
                    stroke: $fontBlack;
                }
            }
        }
    }

}