.cart-card {
    position: relative;
    display: flex;
    align-items: center;
    border-bottom: 1px solid $greyLight;
    padding-right: vw(37);
    gap: vw(50);
    background-color: $white;

    @include max(xl) {
        padding-right: vwf(37);
        gap: vwf(50);
    }

    @include max(md) {
        flex-wrap: wrap;
        padding: 0;
    }

    &__photo {
        width: vw(198);
        flex-basis: vw(198);
        flex-shrink: 0;
        flex-grow: 0;
        height: vw(149);
        padding: vw(12) vw(34) vw(9) vw(30);
        border-right: 1px solid $greyLight;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        @include max(xl) {
            width: vwf(198);
            flex-basis: vwf(198);
            height: vwf(149);
            padding: vwf(12) vwf(34) vwf(9) vwf(30);
        }

        @include max(md) {
            width: vw2(198);
            flex-basis: vw2(198);
            height: vw2(149);
            padding: vw2(10) vw2(20) 0;
            border-right: none;
        }
    }

    &__title {
        flex-basis: auto;
        font-size: vw(24);
        font-weight: 500;
        line-height: 0.958;
        color: $fontBlack;

        @include max(xl) {
            font-size: vwf(24);
        }

        @include max(md) {
            flex-basis: 100%;
            font-size: vw2(24);
            order: 2;
            padding: 0 vw2(20) vw2(20);
        }
    }

    &__controls {
        margin: 0 vw(85) 0 auto;
        min-width: vw(174);
        flex-basis: vw(174);
        flex-shrink: 0;
        flex-grow: 0;

        @include max(xl) {
            margin: 0 vwf(85) 0 auto;
            min-width: vwf(174);
            flex-basis: vwf(174);
        }

        @include max(md) {
            margin: 0;
        }
    }

    &__price {
        font-size: vw(18);
        font-weight: 600;
        line-height: 1.25;
        color: $fontBlack;
        white-space: nowrap;
        margin-top: 7px;

        @include max(xl) {
            font-size: vwf(18);
        }

        @include max(md) {
            font-size: vw2(18);
        }
    }

    &__delete.new-btn {
        margin-top: vw(-18);

        @include max(xl) {
            margin-top: vwf(-18);
        }

        @include max(md) {
            position: absolute;
            right: vw2(10);
            top: vw2(10);
            width: vw2(50);
            height: vw2(50);
            margin-top: vw2(28);
        }

        .new-btn__icon {
            @include max(md) {
                width: vw2(50);
                height: vw2(50);
            }

            svg,
            svg use {
                width: vw(20);
                height: vw(20);
                fill: $white;
                stroke: $white;

                @include max(xl) {
                    width: vwf(20);
                    height: vwf(20);
                }

                @include max(md) {
                    width: vw2(20);
                    height: vw2(20);
                }
            }
        }
    }

}